.Card_Element{
    box-sizing: border-box;
    width:50vw;
    overflow: hidden;
    display: grid;
    border-left: 1px solid white;
    padding: 0px 10px;
    margin-bottom: 50px;
}

.Card_Element img{
    justify-self: center;
    width: 60px;
    display: inline-block;
}

.Card_Element h2{
    display: inline-block;
    position: relative;
    bottom: -3em;
    margin: 0;
    margin-top: 5em;
    font-weight: 100;
}

.Card_Element .activeH2{
    position: static;
    animation: slide_uph2 2s;
}

.Card_Element p{
    height: 5em;
    margin: 0;
}

.Card_Element .activeP{
    animation: slide_up 2s;
    position: static;
}

.Card_Element .inactiveP{
    position: relative;
    left: -5000px;
    
}

@keyframes slide_up {
    from{
        transform: translateY(100%); 
        opacity: 0.2;
    }
    
    to{
        transform: translateY(0%); 
        opacity: 1;

    }
}


@keyframes slide_uph2 {
    from{
        transform: translateY(150%); 
        opacity: 0.2;
    }
    
    to{
        transform: translateY(0%); 
        opacity: 1;

    }
}

